<template>
  <div id="app" class="min-h-screen">
    <nav class="glass sticky top-0 z-50 border-b border-white/20 shadow-lg">
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div class="flex justify-between h-20">
          <div class="flex items-center">
            <router-link to="/courses" class="text-2xl font-extrabold gradient-text hover:scale-105 transition-transform">
              ðŸ“š EduHub
            </router-link>
            <div class="ml-10 flex items-baseline space-x-1">
              <router-link
                v-if="isAuthenticated"
                to="/"
                class="px-4 py-2 rounded-xl text-sm font-semibold text-gray-700 hover:text-primary-600 hover:bg-white/50 transition-all duration-200 relative group"
                active-class="text-primary-600 bg-gradient-to-r from-primary-100 to-accent-100 shadow-md"
              >
                <span class="relative z-10">Dashboard</span>
                <span class="absolute inset-0 rounded-xl bg-gradient-to-r from-primary-500/10 to-accent-500/10 opacity-0 group-hover:opacity-100 transition-opacity"></span>
              </router-link>
              <router-link
                to="/courses"
                class="px-4 py-2 rounded-xl text-sm font-semibold text-gray-700 hover:text-primary-600 hover:bg-white/50 transition-all duration-200 relative group"
                active-class="text-primary-600 bg-gradient-to-r from-primary-100 to-accent-100 shadow-md"
              >
                <span class="relative z-10">Courses</span>
                <span class="absolute inset-0 rounded-xl bg-gradient-to-r from-primary-500/10 to-accent-500/10 opacity-0 group-hover:opacity-100 transition-opacity"></span>
              </router-link>
            </div>
          </div>
          <div class="flex items-center space-x-4">
            <button
              v-if="isAuthenticated"
              @click="logout"
              class="px-6 py-2.5 text-sm font-semibold text-white bg-gradient-to-r from-red-500 to-pink-500 rounded-xl hover:from-red-600 hover:to-pink-600 transition-all duration-200 shadow-lg hover:shadow-xl transform hover:scale-105 active:scale-95"
            >
              Logout
            </button>
          </div>
        </div>
      </div>
    </nav>
    <main class="relative">
      <div class="fixed inset-0 -z-10 bg-gradient-to-br from-purple-50 via-pink-50 to-indigo-50"></div>
      <router-view />
    </main>
  </div>
</template>

<script>
import { computed } from 'vue'
import { useRouter } from 'vue-router'

export default {
  name: 'App',
  setup() {
    const router = useRouter()
    
    const isAuthenticated = computed(() => {
      return !!localStorage.getItem('token')
    })

    const logout = () => {
      localStorage.removeItem('token')
      router.push('/courses')
    }

    return {
      isAuthenticated,
      logout
    }
  }
}
</script>
