<template>
  <div class="min-h-screen py-12 px-4 sm:px-6 lg:px-8">
    <div class="max-w-2xl mx-auto">
      <div class="mb-8 animate-slide-up">
        <button
          @click="$router.push('/')"
          class="flex items-center text-gray-600 hover:text-primary-600 mb-6 transition-colors group"
        >
          <svg class="h-5 w-5 mr-2 group-hover:-translate-x-1 transition-transform" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
          </svg>
          <span class="font-semibold">Back to Dashboard</span>
        </button>
      </div>

      <div class="glass rounded-3xl border border-white/30 shadow-2xl p-10 animate-scale-in">
        <div class="mb-8">
          <div class="inline-flex items-center justify-center w-16 h-16 rounded-2xl bg-gradient-to-br from-primary-500 to-accent-500 mb-4 shadow-lg">
            <svg class="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
            </svg>
          </div>
          <h1 class="text-4xl font-extrabold gradient-text mb-2">Create New Course</h1>
          <p class="text-gray-600">Fill in the details to create a new course</p>
        </div>

        <form @submit.prevent="createCourse" class="space-y-6">
          <div>
            <label for="title" class="block text-sm font-bold text-gray-700 mb-3">
              Course Title <span class="text-red-500">*</span>
            </label>
            <input
              id="title"
              v-model="form.title"
              type="text"
              required
              class="appearance-none block w-full px-5 py-4 border-2 border-gray-200 rounded-xl placeholder-gray-400 text-gray-900 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all bg-white/80 backdrop-blur-sm"
              placeholder="Enter course title"
            />
          </div>

          <div>
            <label for="description" class="block text-sm font-bold text-gray-700 mb-3">
              Description
            </label>
            <textarea
              id="description"
              v-model="form.description"
              rows="4"
              class="appearance-none block w-full px-5 py-4 border-2 border-gray-200 rounded-xl placeholder-gray-400 text-gray-900 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all resize-none bg-white/80 backdrop-blur-sm"
              placeholder="Enter course description"
            ></textarea>
          </div>

          <div>
            <label for="instructor" class="block text-sm font-bold text-gray-700 mb-3">
              Instructor <span class="text-red-500">*</span>
            </label>
            <input
              id="instructor"
              v-model="form.instructor"
              type="text"
              required
              class="appearance-none block w-full px-5 py-4 border-2 border-gray-200 rounded-xl placeholder-gray-400 text-gray-900 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all bg-white/80 backdrop-blur-sm"
              placeholder="Enter instructor name"
            />
          </div>

          <div v-if="error" class="bg-red-50 border-2 border-red-200 text-red-700 px-5 py-4 rounded-xl text-sm font-medium animate-slide-up">
            {{ error }}
          </div>

          <div v-if="success" class="bg-green-50 border-2 border-green-200 text-green-700 px-5 py-4 rounded-xl text-sm font-medium animate-slide-up">
            {{ success }}
          </div>

          <div class="flex items-center justify-end space-x-4 pt-4">
            <button
              type="button"
              @click="$router.push('/')"
              class="px-8 py-3.5 border-2 border-gray-300 rounded-xl text-gray-700 font-semibold hover:bg-gray-50 transition-all"
              :disabled="loading"
            >
              Cancel
            </button>
            <button
              type="submit"
              :disabled="loading || !form.title || !form.instructor"
              class="px-8 py-3.5 bg-gradient-to-r from-primary-600 to-accent-500 text-white rounded-xl hover:from-primary-700 hover:to-accent-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 disabled:opacity-50 disabled:cursor-not-allowed transition-all transform hover:scale-105 active:scale-95 shadow-lg hover:shadow-xl font-bold flex items-center"
            >
              <span v-if="!loading">Create Course</span>
              <span v-else class="flex items-center">
                <svg class="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
                  <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
                Creating...
              </span>
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'CourseCreate',
  data() {
    return {
      form: {
        title: '',
        description: '',
        instructor: ''
      },
      loading: false,
      error: '',
      success: ''
    }
  },
  methods: {
    async createCourse() {
      if (!this.form.title || !this.form.instructor) {
        this.error = 'Please fill in all required fields'
        return
      }

      this.loading = true
      this.error = ''
      this.success = ''

      try {
        await axios.post('/Course', {
          title: this.form.title,
          description: this.form.description || '',
          instructor: this.form.instructor
        })

        this.success = 'Course created successfully!'
        
        setTimeout(() => {
          this.$router.push('/')
        }, 1500)
      } catch (err) {
        this.error = err.response?.data?.message || 'Failed to create course. Please try again.'
        console.error('Create course error:', err)
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
