<template>
  <div class="min-h-screen py-12 px-4 sm:px-6 lg:px-8">
    <div class="max-w-7xl mx-auto">
      <div class="mb-8 animate-slide-up">
        <button
          @click="$router.push('/courses')"
          class="flex items-center text-gray-600 hover:text-primary-600 mb-6 transition-colors group"
        >
          <svg class="h-5 w-5 mr-2 group-hover:-translate-x-1 transition-transform" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
          </svg>
          <span class="font-semibold">Back to Courses</span>
        </button>
        
        <div class="glass rounded-3xl border border-white/30 shadow-2xl p-8 mb-8">
          <h1 class="text-5xl font-extrabold gradient-text mb-4">{{ course?.title || 'Loading...' }}</h1>
          <div v-if="course?.instructor" class="flex items-center mb-4">
            <div class="flex items-center px-4 py-2 rounded-xl bg-gradient-to-r from-primary-100 to-accent-100 border border-primary-200">
              <svg class="h-5 w-5 mr-2 text-primary-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
              </svg>
              <span class="text-lg font-semibold text-gray-700">{{ course.instructor }}</span>
            </div>
          </div>
          <p v-if="course?.description" class="text-lg text-gray-600 leading-relaxed">{{ course.description }}</p>
        </div>
      </div>

      <div class="flex items-center justify-between mb-8 animate-slide-up" style="animation-delay: 0.1s;">
        <h2 class="text-4xl font-extrabold gradient-text">Lectures</h2>
        <router-link
          v-if="isAuthenticated"
          :to="`/upload-lecture/${$route.params.id}`"
          class="inline-flex items-center px-6 py-3 bg-gradient-to-r from-primary-600 to-accent-500 text-white rounded-xl hover:from-primary-700 hover:to-accent-600 transition-all shadow-lg hover:shadow-xl transform hover:scale-105 active:scale-95 font-semibold"
        >
          <svg class="h-5 w-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 4v16m8-8H4" />
          </svg>
          Upload Lecture
        </router-link>
      </div>

      <div v-if="loading" class="flex justify-center items-center py-20">
        <div class="relative">
          <div class="animate-spin rounded-full h-16 w-16 border-4 border-primary-200 border-t-primary-600"></div>
          <div class="absolute inset-0 rounded-full border-4 border-transparent border-t-accent-500 animate-spin" style="animation-duration: 1.5s;"></div>
        </div>
      </div>

      <div v-else-if="error" class="glass rounded-2xl border-2 border-red-200 text-red-700 px-6 py-4 animate-slide-up">
        <div class="flex items-center">
          <svg class="h-5 w-5 mr-2" fill="currentColor" viewBox="0 0 20 20">
            <path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clip-rule="evenodd" />
          </svg>
          {{ error }}
        </div>
      </div>

      <div v-else-if="lectures.length === 0" class="text-center py-20 animate-slide-up">
        <div class="glass rounded-3xl p-12 border border-white/30 max-w-md mx-auto">
          <div class="inline-flex items-center justify-center w-24 h-24 rounded-3xl bg-gradient-to-br from-green-100 to-emerald-100 mb-6">
            <svg class="h-12 w-12 text-green-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
            </svg>
          </div>
          <h3 class="text-2xl font-bold text-gray-900 mb-2">No lectures yet</h3>
          <p class="text-gray-500 mb-6">
            <span v-if="isAuthenticated">Get started by uploading your first lecture.</span>
            <span v-else>No lectures available for this course yet.</span>
          </p>
          <router-link
            v-if="isAuthenticated"
            :to="`/upload-lecture/${$route.params.id}`"
            class="inline-flex items-center px-6 py-3 bg-gradient-to-r from-primary-600 to-accent-500 text-white rounded-xl hover:from-primary-700 hover:to-accent-600 transition-all shadow-lg hover:shadow-xl transform hover:scale-105 font-semibold"
          >
            Upload Lecture
          </router-link>
        </div>
      </div>

      <div v-else class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        <div
          v-for="(lecture, index) in lectures"
          :key="lecture.id"
          class="glass rounded-3xl border border-white/30 shadow-xl overflow-hidden group card-hover animate-slide-up"
          :style="{ animationDelay: `${index * 0.1}s` }"
        >
          <div class="p-8">
            <div class="flex items-start justify-between mb-6">
              <div class="flex-shrink-0 rounded-2xl p-4 bg-gradient-to-br from-green-500 to-emerald-500 group-hover:scale-110 transition-transform duration-300 shadow-lg">
                <svg class="h-8 w-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
              </div>
            </div>
            
            <h3 class="text-2xl font-bold text-gray-900 mb-4 group-hover:gradient-text transition-all duration-300">
              {{ lecture.title || 'Untitled Lecture' }}
            </h3>
            
            <div class="mt-6 pt-6 border-t border-gray-200/50">
              <a
                :href="getFileUrl(lecture.lectureUrl)"
                target="_blank"
                class="inline-flex items-center w-full justify-center px-6 py-3 bg-gradient-to-r from-primary-600 to-accent-500 text-white text-sm font-semibold rounded-xl hover:from-primary-700 hover:to-accent-600 transition-all shadow-lg hover:shadow-xl transform hover:scale-105 active:scale-95"
                @click.stop
              >
                <svg class="h-5 w-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M10 6H6a2 2 0 00-2 2v10a2 2 0 002 2h10a2 2 0 002-2v-4M14 4h6m0 0v6m0-6L10 14" />
                </svg>
                Open Lecture
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { getFileUrl } from '../utils/urlHelper'

export default {
  name: 'CourseDetail',
  data() {
    return {
      course: null,
      lectures: [],
      loading: false,
      error: ''
    }
  },
  computed: {
    isAuthenticated() {
      return !!localStorage.getItem('token')
    }
  },
  async created() {
    await this.loadData()
  },
  watch: {
    '$route.params.id'() {
      this.loadData()
    }
  },
  methods: {
    getFileUrl,
    async loadData() {
      this.loading = true
      this.error = ''
      const courseId = this.$route.params.id
      
      try {
        const [courseRes, lecturesRes] = await Promise.all([
          axios.get(`/Course/${courseId}`),
          axios.get('/Lecture')
        ])
        
        this.course = courseRes.data?.data || null
        const allLectures = lecturesRes.data?.data || []
        this.lectures = allLectures.filter(l => l.courseId === courseId)
      } catch (err) {
        this.error = err.response?.data?.message || 'Failed to load course details'
        console.error('Error loading course:', err)
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
