import axios from 'axios'

/**
 * Constructs the full URL for uploaded files
 * @param {string} filename - The filename returned from the API
 * @returns {string} - The full URL to the file
 */
export function getFileUrl(filename) {
  if (!filename) return ''
  
  // If filename already contains http/https, return as is
  if (filename.startsWith('http://') || filename.startsWith('https://')) {
    return filename
  }
  
  // Get base URL from axios defaults and remove /api
  const baseURL = axios.defaults.baseURL || '/api'
  const baseWithoutApi = baseURL.replace('/api', '')
  
  // Remove leading slash from filename if present
  const cleanFilename = filename.startsWith('/') ? filename.slice(1) : filename
  
  // Construct URL: baseURL (without /api) + /uploads/filename
  return `${baseWithoutApi}/uploads/${cleanFilename}`
}

