</template>

<script>
import axios from 'axios'

export default {
  name: 'Dashboard',
  data() {
    return {
      stats: {
        totalCourses: 0,
        totalLectures: 0
      },
      loading: false
    }
  },
  async created() {
    await this.loadStats()
  },
  methods: {
    async loadStats() {
      this.loading = true
      try {
        const [coursesRes, lecturesRes] = await Promise.all([
          axios.get('/Course'),
          axios.get('/Lecture')
        ])
        
        this.stats.totalCourses = coursesRes.data?.data?.length || 0
        this.stats.totalLectures = lecturesRes.data?.data?.length || 0
      } catch (error) {
        console.error('Failed to load stats:', error)
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
