import { createRouter, createWebHistory } from 'vue-router'

import Login from '../components/Login.vue'
import Dashboard from '../components/Dashboard.vue'
import Courses from '../components/Courses.vue'
import CourseDetail from '../components/CourseDetail.vue'
import LectureUpload from '../components/LectureUpload.vue'
import CourseCreate from '../components/CourseCreate.vue'

const routes = [
  { path: '/login', component: Login },
  { 
    path: '/', 
    component: Dashboard,
    meta: { requiresAuth: true }
  },
  {
    path: '/courses',
    component: Courses
  },
  {
    path: '/courses/:id',
    component: CourseDetail
  },
  {
    path: '/upload-lecture/:courseId',
    component: LectureUpload,
    meta: { requiresAuth: true }
  },
  {
    path: '/create-course',
    component: CourseCreate,
    meta: { requiresAuth: true }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('token')
  
  // Redirect root path to courses for unauthenticated users, dashboard for authenticated
  if (to.path === '/') {
    if (token) {
      return next() // Allow access to dashboard
    } else {
      return next('/courses') // Redirect to courses for unauthenticated users
    }
  }
  
  // Redirect to login if route requires auth and user is not authenticated
  if (to.meta.requiresAuth && !token) {
    return next('/login')
  }
  
  // Redirect to dashboard if user is authenticated and tries to access login
  if (to.path === '/login' && token) {
    return next('/')
  }
  
  next()
})

export default router
