<template>
  <div class="min-h-screen py-12 px-4 sm:px-6 lg:px-8">
    <div class="max-w-2xl mx-auto">
      <div class="mb-8 animate-slide-up">
        <button
          @click="$router.push(`/courses/${$route.params.courseId}`)"
          class="flex items-center text-gray-600 hover:text-primary-600 mb-6 transition-colors group"
        >
          <svg class="h-5 w-5 mr-2 group-hover:-translate-x-1 transition-transform" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
          </svg>
          <span class="font-semibold">Back to Course</span>
        </button>
      </div>

      <div class="glass rounded-3xl border border-white/30 shadow-2xl p-10 animate-scale-in">
        <div class="mb-8">
          <div class="inline-flex items-center justify-center w-16 h-16 rounded-2xl bg-gradient-to-br from-green-500 to-emerald-500 mb-4 shadow-lg">
            <svg class="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
            </svg>
          </div>
          <h1 class="text-4xl font-extrabold gradient-text mb-2">Upload Lecture</h1>
          <p class="text-gray-600">Upload a new lecture file for this course</p>
        </div>

        <form @submit.prevent="upload" class="space-y-6">
          <div>
            <label for="title" class="block text-sm font-bold text-gray-700 mb-3">
              Lecture Title <span class="text-red-500">*</span>
            </label>
            <input
              id="title"
              v-model="form.title"
              type="text"
              required
              class="appearance-none block w-full px-5 py-4 border-2 border-gray-200 rounded-xl placeholder-gray-400 text-gray-900 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all bg-white/80 backdrop-blur-sm"
              placeholder="Enter lecture title"
            />
          </div>

          <div>
            <label for="file" class="block text-sm font-bold text-gray-700 mb-3">
              Lecture File <span class="text-red-500">*</span>
            </label>
            <div class="mt-1 flex justify-center px-6 pt-8 pb-8 border-2 border-dashed border-gray-300 rounded-2xl hover:border-primary-400 transition-all bg-white/50 backdrop-blur-sm hover:bg-white/70">
              <div class="space-y-2 text-center">
                <svg
                  v-if="!form.file"
                  class="mx-auto h-16 w-16 text-gray-400"
                  stroke="currentColor"
                  fill="none"
                  viewBox="0 0 48 48"
                >
                  <path
                    d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
                    stroke-width="2"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  />
                </svg>
                <div v-if="!form.file" class="flex text-sm text-gray-600 justify-center">
                  <label
                    for="file-upload"
                    class="relative cursor-pointer bg-gradient-to-r from-primary-600 to-accent-500 rounded-xl px-6 py-3 text-white font-semibold hover:from-primary-700 hover:to-accent-600 transition-all shadow-lg hover:shadow-xl transform hover:scale-105"
                  >
                    <span>Upload a file</span>
                    <input
                      id="file-upload"
                      name="file-upload"
                      type="file"
                      class="sr-only"
                      @change="setFile"
                      accept=".pdf,.doc,.docx,.ppt,.pptx,.mp4,.mp3"
                    />
                  </label>
                  <p class="pl-3 flex items-center text-gray-500">or drag and drop</p>
                </div>
                <div v-else class="text-sm">
                  <div class="flex items-center justify-center mb-2">
                    <svg class="h-8 w-8 text-green-500 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                    <p class="font-bold text-gray-900 text-lg">{{ form.file.name }}</p>
                  </div>
                  <p class="text-gray-500 mb-3">{{ formatFileSize(form.file.size) }}</p>
                  <button
                    type="button"
                    @click="form.file = null"
                    class="text-red-600 hover:text-red-700 text-sm font-semibold hover:underline"
                  >
                    Remove
                  </button>
                </div>
                <p class="text-xs text-gray-400 pt-2">PDF, DOC, PPT, MP4, MP3 up to 100MB</p>
              </div>
            </div>
          </div>

          <div v-if="error" class="bg-red-50 border-2 border-red-200 text-red-700 px-5 py-4 rounded-xl text-sm font-medium animate-slide-up">
            {{ error }}
          </div>

          <div v-if="success" class="bg-green-50 border-2 border-green-200 text-green-700 px-5 py-4 rounded-xl text-sm font-medium animate-slide-up">
            {{ success }}
          </div>

          <div class="flex items-center justify-end space-x-4 pt-4">
            <button
              type="button"
              @click="$router.push(`/courses/${$route.params.courseId}`)"
              class="px-8 py-3.5 border-2 border-gray-300 rounded-xl text-gray-700 font-semibold hover:bg-gray-50 transition-all"
              :disabled="loading"
            >
              Cancel
            </button>
            <button
              type="submit"
              :disabled="loading || !form.file || !form.title"
              class="px-8 py-3.5 bg-gradient-to-r from-primary-600 to-accent-500 text-white rounded-xl hover:from-primary-700 hover:to-accent-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 disabled:opacity-50 disabled:cursor-not-allowed transition-all transform hover:scale-105 active:scale-95 shadow-lg hover:shadow-xl font-bold flex items-center"
            >
              <span v-if="!loading">Upload Lecture</span>
              <span v-else class="flex items-center">
                <svg class="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
                  <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
                Uploading...
              </span>
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'LectureUpload',
  data() {
    return {
      form: {
        title: '',
        file: null
      },
      loading: false,
      error: '',
      success: ''
    }
  },
  methods: {
    setFile(event) {
      const file = event.target.files?.[0]
      if (file) {
        if (file.size > 100 * 1024 * 1024) {
          this.error = 'File size must be less than 100MB'
          return
        }
        this.form.file = file
        this.error = ''
      }
    },
    formatFileSize(bytes) {
      if (bytes === 0) return '0 Bytes'
      const k = 1024
      const sizes = ['Bytes', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return Math.round(bytes / Math.pow(k, i) * 100) / 100 + ' ' + sizes[i]
    },
    async upload() {
      if (!this.form.file || !this.form.title) {
        this.error = 'Please fill in all required fields'
        return
      }

      this.loading = true
      this.error = ''
      this.success = ''

      try {
        const formData = new FormData()
        formData.append('file', this.form.file)

        const uploadRes = await axios.post('/Attachments/file', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })

        const fileUrl = uploadRes.data?.data
        if (!fileUrl) {
          throw new Error('Failed to get file URL from upload response')
        }

        await axios.post('/Lecture', {
          title: this.form.title,
          courseId: this.$route.params.courseId,
          lectureUrl: fileUrl
        })

        this.success = 'Lecture uploaded successfully!'
        
        setTimeout(() => {
          this.$router.push(`/courses/${this.$route.params.courseId}`)
        }, 1500)
      } catch (err) {
        this.error = err.response?.data?.message || err.message || 'Failed to upload lecture. Please try again.'
        console.error('Upload error:', err)
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
