<template>
  <div class="min-h-screen py-12 px-4 sm:px-6 lg:px-8">
    <div class="max-w-7xl mx-auto">
      <!-- Header -->
      <div class="mb-12 animate-slide-up">
        <h1 class="text-6xl font-extrabold gradient-text mb-4">Courses</h1>
        <p class="text-xl text-gray-600">Browse and explore our amazing courses</p>
      </div>

      <div v-if="loading" class="flex justify-center items-center py-20">
        <div class="relative">
          <div class="animate-spin rounded-full h-16 w-16 border-4 border-primary-200 border-t-primary-600"></div>
          <div class="absolute inset-0 rounded-full border-4 border-transparent border-t-accent-500 animate-spin" style="animation-duration: 1.5s;"></div>
        </div>
      </div>

      <div v-else-if="error" class="glass rounded-2xl border-2 border-red-200 text-red-700 px-6 py-4 animate-slide-up">
        <div class="flex items-center">
          <svg class="h-5 w-5 mr-2" fill="currentColor" viewBox="0 0 20 20">
            <path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clip-rule="evenodd" />
          </svg>
          {{ error }}
        </div>
      </div>

      <div v-else-if="courses.length === 0" class="text-center py-20 animate-slide-up">
        <div class="glass rounded-3xl p-12 border border-white/30 max-w-md mx-auto">
          <div class="inline-flex items-center justify-center w-24 h-24 rounded-3xl bg-gradient-to-br from-primary-100 to-accent-100 mb-6">
            <svg class="h-12 w-12 text-primary-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
            </svg>
          </div>
          <h3 class="text-2xl font-bold text-gray-900 mb-2">No courses yet</h3>
          <p class="text-gray-500">Get started by creating a new course.</p>
        </div>
      </div>

      <div v-else class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        <div
          v-for="(course, index) in courses"
          :key="course.id"
          class="glass rounded-3xl border border-white/30 shadow-xl overflow-hidden group cursor-pointer card-hover animate-slide-up"
          :style="{ animationDelay: `${index * 0.1}s` }"
          @click="$router.push(`/courses/${course.id}`)"
        >
          <div class="p-8">
            <div class="flex items-start justify-between mb-6">
              <div class="flex-shrink-0 rounded-2xl p-4 bg-gradient-to-br from-primary-500 to-accent-500 group-hover:scale-110 transition-transform duration-300 shadow-lg">
                <svg class="h-8 w-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
                </svg>
              </div>
            </div>
            
            <h3 class="text-2xl font-bold text-gray-900 mb-3 group-hover:gradient-text transition-all duration-300">
              {{ course.title || 'Untitled Course' }}
            </h3>
            
            <p v-if="course.instructor" class="text-sm font-semibold text-primary-600 mb-3 flex items-center">
              <svg class="h-4 w-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
              </svg>
              {{ course.instructor }}
            </p>
            
            <p v-if="course.description" class="text-gray-600 text-sm mb-6 line-clamp-2">
              {{ course.description }}
            </p>
            
            <div class="flex items-center justify-between pt-6 border-t border-gray-200/50">
              <span class="text-sm font-semibold text-gray-500 flex items-center">
                <svg class="h-4 w-4 mr-1.5 text-primary-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
                {{ getLectureCount(course.id) }} lectures
              </span>
              <div class="flex items-center text-primary-600 group-hover:text-accent-600 transition-colors">
                <span class="text-sm font-semibold mr-2">View</span>
                <svg class="h-5 w-5 group-hover:translate-x-1 transition-transform" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
                </svg>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Courses',
  data() {
    return {
      courses: [],
      lectures: [],
      loading: false,
      error: ''
    }
  },
  async created() {
    await this.loadData()
  },
  methods: {
    async loadData() {
      this.loading = true
      this.error = ''
      
      try {
        const [coursesRes, lecturesRes] = await Promise.all([
          axios.get('/Course'),
          axios.get('/Lecture')
        ])
        
        this.courses = coursesRes.data?.data || []
        this.lectures = lecturesRes.data?.data || []
      } catch (err) {
        this.error = err.response?.data?.message || 'Failed to load courses'
        console.error('Error loading courses:', err)
      } finally {
        this.loading = false
      }
    },
    getLectureCount(courseId) {
      return this.lectures.filter(l => l.courseId === courseId).length
    }
  }
}
</script>
